import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, LoadingController, AlertController } from 'ionic-angular';
import { Camera, CameraOptions } from '@ionic-native/camera';
import { BarcodeScanner } from '@ionic-native/barcode-scanner';

import { HTTP } from '@ionic-native/http';

import { Connect } from '../../providers/connect';

/**
 * Generated class for the DosimPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-dosim',
  templateUrl: 'dosim.html',
})
export class DosimPage {
  public odd : any = 1;
  public title : any = 'WHITENING';
  public blurb : any = 'A';
  public before : any = './assets/img/before0.jpg';
  public after : any = './assets/img/after0.jpg';
  public src : any = './assets/img/before0.jpg';
  public answers : any = [0,0,0,0];
  public timer : any;
  public baf : any = 'BEFORE';
  public comments : any = '';
  public mode : any = 0;
  public pl : any;
  public instructions : any = '...';

  public data = [
    {'title' : 'WHITENING', 'blurb' : 'Are you concerned about the colour of your teeth?'},
    {'title' : 'VENEERS', 'blurb' : 'Are you concerned about the shape of your teeth?'},
    {'title' : 'ORTHODONTICS', 'blurb' : 'Are you concerned about any crooked teeth?'},
    {'title' : 'COSMETICS', 'blurb' : 'Are you concerned about any gaps in your teeth?'}
  ];

  public current : any = 0;

  constructor(public navCtrl: NavController, public navParams: NavParams, private barcodeScanner: BarcodeScanner, public connect : Connect, private camera: Camera, public http : HTTP, public loadingCtrl: LoadingController, public alertCtrl : AlertController) {
    this.pl = {
      uniq : 0,
      ans : '',
      picdata : '',
      comments : '',
      dcode : '',
      suburb : '',
      state : '',
      country : '',
      name : '',
      email : '',
      pushid : '',
      phone : '',
      dcodestat : 0
    };
    this.comments='';
  }

 
  ionViewDidEnter() {
     this.current=0;
     this.odd=1;
     this.mode=0;
     this.shownum();
     let that=this;

     let den = window.localStorage.getItem('dcode');
     if (!den) den='';
     this.pl.dcode = den;

     that.timer = setInterval(function () {
       that.odd = 1 - that.odd;
       that.src = (that.odd ? that.before : that.after);
       that.baf = (that.odd ? 'BEFORE' : 'AFTER');
     }, 2500);
     this.pl.uniq = window.localStorage.getItem('myac');
       let url = 'instructions.php?end='+Math.random();
        this.connect.getList(url).subscribe(
         data => {
            that.instructions = data.instructions;
         },
         err => {
         }
        ); 

  }

  gonxt(a)
  { this.answers[this.current]=a;
    if (this.current++>=3) {
       this.takeselfie();
    }
    else 
     this.shownum();
  }

  goback()
  { this.current--;
    this.shownum();
  }

  shownum()
  { this.title = this.data[this.current].title;
    this.blurb = this.data[this.current].blurb;
    this.before = './assets/img/before'+this.current.toString()+'.jpg';
    this.after = './assets/img/after'+this.current.toString()+'.jpg';
    this.src = (this.odd ? this.before : this.after);
   
  }
 
  takeselfie()
  {  this.title='SELFIE TIME!';
     this.mode=1;
 
  }

  pdecode(pwd)
  {
    let l = parseInt(pwd.substr(0,3));
    pwd=pwd.substr(3);

    let d=pwd.length / 12;
    
    let juggleback = [9,4,10,1,8,6,3,2,7,5,0,11];
    let p = '';

    for (let i =0; i< d; i++) {
      let pp=pwd.substr(i*12,12);
      for (let e=0; e<12; e++) 
       p=p + pp.substr(juggleback[e],1);
    }
    return p.substr(0,l);
  }


  scanqr()
  { this.barcodeScanner.scan().then((barcodeData) => {
      let t=this.pdecode(barcodeData.text);
      let w=t.split(',');
      while (w.length<5) w.push('');
      this.pl.name=w[0];
      this.pl.email=w[1];
      this.pl.phone=w[2];
      this.pl.dcode=w[3];
      window.localStorage.setItem('dcode',w[3]); 
    }, (err) => {
 //      alert('err: '+JSON.stringify(err));
    }
   );
  }

   takepicture()
  {   const options: CameraOptions = {
        quality: 100,
        targetWidth:500,
        targetHeight:650, 
        correctOrientation:true,
        destinationType: this.camera.DestinationType.DATA_URL,
        cameraDirection: this.camera.Direction.FRONT,
        encodingType: this.camera.EncodingType.JPEG,
        mediaType: this.camera.MediaType.PICTURE
      }
      this.camera.getPicture(options).then((imageData) => {
        this.pl.picdata = imageData;
        this.title='LASTLY, YOUR DETAILS';
        this.mode=2;
      }, (err) => {
         alert(JSON.stringify(err));
      });
 
  }


  cheat()
  {
       this.title='LASTLY, YOUR DETAILS';
        this.mode=2;
  }

  nocode()
  { this.pl.dcodestat = 1;
  }

  yescode()
  { this.pl.dcodestat = 0;
  }

  doComplete(a) {
    let that=this;
    let alert = this.alertCtrl.create({
      title: 'Thank you - All Done',
      subTitle: 'Track your photos in the See My Simulations tab below',
      buttons: [
        {
          text: 'Okay',
	  role: 'cancel',
          handler: () => {
             this.navCtrl.pop();
          }
        }
      ]
    });
    alert.present();
  }

  saveme()
  {
      this.pl.me = window.localStorage.getItem('uniq');
      this.pl.ans = '' + this.answers[0].toString() + this.answers[1].toString() + this.answers[2].toString() + this.answers[3].toString();
      let pi = window.localStorage.getItem('pushid');
      if (!pi) pi='';
      this.pl.pushid = pi;
      let that = this;
       let loading = this.loadingCtrl.create({
         content: 'Please wait...'
       });
       loading.present();
       let url = 'http://smilesim.com.au/server/upload.php';
       this.http.post(url, that.pl, {})
         .then(data => {
            loading.dismiss();
 	    that.doComplete(JSON.stringify(data));
         })
         .catch(error => {
            alert(JSON.stringify(error));
            loading.dismiss();
     
         });
     
  }

}




