import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { Connect } from '../../providers/connect';
import { ResultPage } from '../result/result';

@IonicPage()
@Component({
  selector: 'page-mysims',
  templateUrl: 'mysims.html',
})
export class MysimsPage {

  cards : any = [];
  me : any = '';
  instr : any = '';
  constructor(public navCtrl: NavController, public navParams: NavParams, private connect : Connect) {
     this.cards=[];
  }

  ionViewDidLoad() {
    this.cards=[];
    this.instr='';
    this.me=window.localStorage.getItem('uniq');
    let that=this;
       let url = 'mysims.php?me='+window.localStorage.getItem('uniq')+'&end='+Math.random();
        this.connect.getList(url).subscribe(
         data => {
            that.cards = data.cards;
            that.instr = data.instr;
         },
         err => {
         }
        ); 

  }

 goPic(i)
 { this.navCtrl.push( ResultPage, { id : i});
 }

}



