import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { Connect } from '../../providers/connect';

/**
 * Generated class for the ResultPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-result',
  templateUrl: 'result.html',
})
export class ResultPage {
  title : any = '';
  pic : any = './assets/img/blank.jpg';
  pic2 : any = './assets/img/blank.jpg';
  text : any = '';
  name : any = '';
  date : any = '';
  result : any = 0;
  constructor(public navCtrl: NavController, public navParams: NavParams, private connect : Connect) {
  }

  ionViewDidLoad() {
    let that=this;
       let url = 'getresult.php?id='+this.navParams.get('id');
        this.connect.getList(url).subscribe(
         data => {
console.log(data);
            that.title = data.title;
            that.pic = data.pic;
            that.name = data.name;
            that.date = data.date;
            that.pic2 = data.pic2;
            that.result = data.result;
            that.text = data.text;
         },
         err => {
         }
        ); 

  }

}



